import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import "./styles/app.sass";
import { useEffect } from "react";
import Page from "./components/ZPage";
import Home from "./screens/ZHome";
import Buy from "./screens/ZHomeBuy";
import Sell from "./screens/ZHomeSell";
import Auction from "./screens/ZHomeAuction";
import Faq from "./screens/ZFaq";
import Contact from "./screens/ZContact";
import ConnectWallet from "./screens/ZConnectWallet";
import About from "./screens/ZAbout";
import Customize from "./screens/ZCustomize";

import UploadVariants from "./screens/UploadVariants";
import UploadDetails from "./screens/UploadDetails";
import Activity from "./screens/Activity";
import Search01 from "./screens/Search01";
import Search02 from "./screens/Search02";
import Profile from "./screens/Profile";
import ProfileEdit from "./screens/ProfileEdit";
import Item from "./screens/Item";
import PageList from "./screens/PageList";
import Control from "./components/ZControl";
import PrivateRouter from "./utils/hooks/PrivateRouter";
import { useEagerConnect } from "./utils/hooks/use-connectors";
import CONNECTOR_NAMES from "./utils/constants/connectorNames";
import { useWeb3React } from "@web3-react/core";
// import Upload from "./screens/UploadDetails";

function SingleMint() {
  return (
    <>
      <h2>Single Mint</h2>
    </>
  );
}
function MultipleMint() {
  return (
    <>
      <h2>Multiple Mint</h2>
    </>
  );
}

function App() {
  const { account, chainId, active } = useWeb3React();
  const { login } = useEagerConnect(
    parseInt(localStorage.getItem("chainId")) ||
      parseInt(process.env.REACT_APP_DEFAULT_CHAINID),
    CONNECTOR_NAMES.injected
  );

  useEffect(() => {
    if (!account && !!localStorage.getItem("chainId")) {
      login();
    }
  }, [account, login]);
  return (
    <Router>
      <Switch>
        <Route
          exact
          path="/"
          component={() => (
            <Page>
              <Home />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/buy"
          component={() => (
            <Page>
              <Home />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/sell"
          component={() => (
            <Page>
              <Home />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/asset/:tokenId"
          component={() => (
            <Page>
              <Item />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/auction"
          component={() => (
            <Page>
              <Home />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/faq"
          component={() => (
            <Page>
              <Faq />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/contact"
          component={() => (
            <Page>
              <Contact />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/customize"
          component={() => (
            <Page>
              <Customize />
            </Page>
          )}
        />

        <PrivateRouter
          exact
          path="/about"
          component={() => (
            <Page>
              <About />
            </Page>
          )}
        />
        <PrivateRouter
          path="/profile"
          component={() => (
            <Page>
              <Profile />
            </Page>
          )}
        />

        <Route
          exact
          path="/connect-wallet"
          component={() => (
            <Page>
              <ConnectWallet />
            </Page>
          )}
        />

        <PrivateRouter
          exact
          path="/mint-options"
          component={() => (
            <Page>
              <UploadVariants />
            </Page>
          )}
        />

        <PrivateRouter
          exact
          path="/mint-single"
          component={() => (
            <Page>
              <UploadDetails />
            </Page>
          )}
        />
        <PrivateRouter
          exact
          path="/mint-multiple"
          component={() => (
            <Page>
              <MultipleMint />
            </Page>
          )}
        />

        {/* 
        <Route exact path="/upload-variants" render={() => (<Page><UploadVariants /></Page>)}/>
        <Route exact path="/upload-details" render={() => (<Page><UploadDetails /></Page>)}/>
        <Route exact path="/connect-wallet" render={() => (<Page><ConnectWallet /></Page>)}/>
        <Route exact path="/activity" render={() => (<Page><Activity /></Page>)}/>
        <Route exact path="/search01" render={() => (<Page><Search01 /></Page>)}/>
        <Route exact path="/search02" render={() => (<Page><Search02 /></Page>)}/>
        <Route exact path="/profile" render={() => (<Page><Profile /></Page>)}/>
        <Route exact path="/profile-edit" render={() => (<Page><ProfileEdit /></Page>)}/>
        <Route exact path="/item" render={() => (<Page><Item /></Page>)}/>
        <Route exact path="/pagelist" render={() => (<Page><PageList /></Page>)}/>
        */}
      </Switch>
    </Router>
  );
}

export default App;
